
import { Vue, Component, Prop, Watch } from "vue-property-decorator";

@Component
export default class Pagination extends Vue {
  @Prop() count!: number;
  @Prop() max!: number;
  @Prop() page!: number;

  private previosPage() {
    if (this.$data.page > 1) this.$data.page -= 1;
    this.$emit("prev", this.$data.page);
  }

  private nextPage() {
    this.$data.page += 1;
    this.$emit("next", this.$data.page);
  }

}
